#headspace {
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 1;
}
#Logo {
    height: 125px;
    border-radius: .25em;
    margin-top: 5%;
}
.devWrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1%;
}
@media only screen and (max-width: 720px) {
    #Logo {
        display: none;
    }
}
@media only screen and (max-width: 350px) {
    #headspace {
        flex-direction: column;
        margin-top: 20%;
        margin-right: 0%;
    }
}
#navigation {
    padding: 10px 0px 10px 0px;
    margin-bottom:1.5% ;
    display: flex;
    flex-direction: row;
    list-style-type: none;
    justify-content: space-evenly;
    align-items: center;
}
#navigation li a {
    color: #000;
    padding: 5px 15px;
    text-decoration: none;
    font-weight: bold;
    font-family: 'Orbitron', sans-serif; 
}
#navigation li a.active {
    background-color: #44566b;
    color: white;
}
.resume_button {
    border-radius: 1em;
    padding: 5px 15px;
    background-color: #EAF2F8;
}
#NavBarFull {
    display: flex;
    flex-direction: column;
    position: fixed;
    width: 100%;
    font-size: 85%;
    overflow: auto;
    opacity: .95;
    z-index: 1;
    top: 0;
}
@Media only screen and (min-width:721px){
    #Hamburger_Menu {
    display: none;
}
}
@media only screen and (max-width: 720px) {
    #navigation {
        display: none;
    }
    #Hamburger_Menu {
        display: block;
        justify-content: flex-start;
        padding: 2%;
    }
    #NavBarFull {
        flex-direction: row;
    }
    .navBar {
        position: relative;
    }
    .navBar button {
        position: fixed;
        left: 2.5%;
        top: 2.5%;
        z-index: 1;
        cursor: pointer;
    }
    .menuNav {
        overflow-y: scroll;
        list-style: none;
        position: fixed;
        top: 0;
        background: #4f6173;
        left: 0;
        bottom: 0;
        height: 100vh;
        width: 0;
        overflow: hidden;
        max-width: 290px;
        z-index: 9;
        transition: .75s;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .menuNav.showMenu {
        width: 100%;
    }
    a {
        display: block;
        padding: 10px 40px;
        text-decoration: none;
        color: #52f97e;
        text-transform: uppercase;
        font-weight: bold;
    }
    .menuNav li:first-child {
        margin-top: 7rem;
    }
    .navBar button {
        background: transparent;;
        border: none;
    }
}
.toggle{
    height: 25px;
    width: 50px;
    border-radius:12.5px;
    position: relative;
    background-image: linear-gradient(aqua, skyblue);
    cursor: pointer;
}
.toggle:hover { 
    box-shadow: 0 0 2px rgba(33,33,33,1);
}
.toggle.night {
    background-image: linear-gradient(midnightblue,rebeccapurple);
    box-shadow: 0 0 3px whitesmoke;
}
.notch {
    height:22.5px;
    width: 22.5px;
    border-radius: 50%; 
    background: yellow;
    position: absolute;
    top: 1.25px;
    left: 1.25px;
    box-shadow: 0 0 1.25px yellow;
    transition: all 0.3s ease-in-out;
}
.notch > .crater {
    background: burlywood;
    border-radius: 50%;
    position: absolute;
    opacity: 0;
    box-shadow: 0 1.25px 1.25px rgba(0,0,0,0.4) inset;
}
.night .crater {
    opacity: 0.4;
}
.crater:first-child {
    left: 1.25px;
    top: 3.75px;
    height: 3.75px;
    width: 10px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
.crater:last-child {
    right: 2.5px;
    top: 3.75px;
    height: 3.75px;
    width: 6.26px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.night > .notch {
    background: whitesmoke;
    box-shadow: 0 0 1.25px whitesmoke;
    -webkit-transform: translate(25px, 0);
            transform: translate(25px, 0);
}

